import { Menu } from './../../shared/menu';
import { ApiService } from './../../shared/api.service';
import { Component, ViewChild, OnInit } from '@angular/core';
import { MatPaginator, MatTableDataSource } from '@angular/material';

@Component({
  selector: 'app-menu-list',
  templateUrl: './menu-list.component.html',
  styleUrls: ['./menu-list.component.scss']
})
export class MenuListComponent implements OnInit {
  MenuData: any = [];
  dataSource: MatTableDataSource<Menu>;
  @ViewChild(MatPaginator,{static:false}) paginator: MatPaginator;
  displayedColumns: string[] = ['_id', 'menu_name', 'menu_email', 'section', 'action'];

  constructor(private menuApi: ApiService) {
    this.menuApi.GetMenus().subscribe(data => {
      this.MenuData = data;
      this.dataSource = new MatTableDataSource<Menu>(this.MenuData);
      setTimeout(() => {
        this.dataSource.paginator = this.paginator;
      }, 0);
    })    
  }

  ngOnInit() { }

  deleteMenu(index: number, e){
    if(window.confirm('Are you sure')) {
      const data = this.dataSource.data;
      data.splice((this.paginator.pageIndex * this.paginator.pageSize) + index, 1);
      this.dataSource.data = data;
      this.menuApi.DeleteMenu(e._id).subscribe()
    }
  }

}