import { Router } from '@angular/router';
import { Component, OnInit, ViewChild, NgZone } from '@angular/core';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatChipInputEvent } from '@angular/material';
import { ApiService } from './../../shared/api.service';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";


@Component({
  selector: 'app-add-menuheader',
  templateUrl: './add-menuheader.component.html',
  styleUrls: ['./add-menuheader.component.scss']
})
export class AddMenuheaderComponent implements OnInit {

  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  @ViewChild('chipList',{static:false}) chipList;
  @ViewChild('resetMenuForm',{static:false}) myNgForm;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  menuForm: FormGroup;
  SectioinArray: any = ['A', 'B', 'C', 'D', 'E'];

  ngOnInit() {
    this.submitBookForm();
  }

  constructor(
    public fb: FormBuilder,
    private router: Router,
    private ngZone: NgZone,
    private menuApi: ApiService
  ) { }

  /* Reactive book form */
  submitBookForm() {
    this.menuForm = this.fb.group({
      menu_header_name: ['', [Validators.required]],
      menu_email: ['', [Validators.required]],
      section: ['', [Validators.required]],
      dob: ['', [Validators.required]],
      foodType: ['veg']
    })
  }

  /* Add dynamic languages */
  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;
    // Add language
    // Reset the input value
    if (input) {
      input.value = '';
    }
  }


  /* Date */
  formatDate(e) {
    var convertDate = new Date(e.target.value).toISOString().substring(0, 10);
    this.menuForm.get('dob').setValue(convertDate, {
      onlyself: true
    })
  }  

  /* Get errors */
  public handleError = (controlName: string, errorName: string) => {
    return this.menuForm.controls[controlName].hasError(errorName);
  }  

  /* Submit book */
  submitMenuForm() {
    if (this.menuForm.valid) {
      this.menuApi.AddMenu(this.menuForm.value).subscribe(res => {
        this.ngZone.run(() => this.router.navigateByUrl('/menus-list'))
      });
    }
  }


}
