import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class UserMenuBottomSheetService {
  public itemId: String ;
  public itemName: String;
  constructor() { }

  public getItemId()
  {
    return this.itemId;
  }

  public setItemId(selectedItemId)
  {
    this.itemId=selectedItemId;
  }
  public getItemName()
  {
    return this.itemName;
  }

  public setItemName(selectedItemName)
  {
    console.log("title setItemName : "+selectedItemName);
    this.itemName=selectedItemName;
  }
}
