import { OwnerHomeComponent } from './Owner/owner-home/owner-home.component';
import { OwnerLoginComponent } from './Owner/owner-login/owner-login.component';
import { LoginComponent } from './User/login/login.component';
import { HomeComponent } from './home/home.component';
import { UserMenusComponent } from './User/user-menus/user-menus.component';
import { ScanQrcodeComponent } from './User/scan-qrcode/scan-qrcode.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { AddMenuComponent } from './Owner/Menu-Book/add-menu/add-menu.component';
import { AddMenuheaderComponent } from './Owner/Menu-Book/add-menuheader/add-menuheader.component';
import { EditMenuComponent } from './Owner/Menu-Book/edit-menu/edit-menu.component';
import { MenuListComponent } from './Owner/Menu-Book/menu-list/menu-list.component';


const routes: Routes = [
  { path: '', component: HomeComponent },
  {
    path:'scan-qr',component:ScanQrcodeComponent
  },
  {
    path:'user-menu',component:UserMenusComponent
  },
  {
    path:'login' ,component:LoginComponent
  },
  {
    path:'owner-login' ,component:OwnerLoginComponent
  },
  {
    path:'owner-home' ,component:OwnerHomeComponent ,children: [
      { path: 'add-menu', component: AddMenuComponent, outlet: 'ownerHome'},
      { path: 'add-menuHeader', component: AddMenuheaderComponent, outlet: 'ownerHome' },
      { path: 'edit-menu/:id', component: EditMenuComponent, outlet: 'ownerHome' },
      { path: 'menu-list', component: MenuListComponent, outlet: 'ownerHome' }]
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
