import { ApiService } from './../../shared/api.service';
import { Router } from '@angular/router';
import { Component, OnInit, ViewChild, NgZone } from '@angular/core';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatChipInputEvent } from '@angular/material';
import { FormGroup,FormControl, FormBuilder, Validators } from "@angular/forms";





export interface Subject {
  name: string;
}

@Component({
  selector: 'app-add-menu',
  templateUrl: './add-menu.component.html',
  styleUrls: ['./add-menu.component.scss']
})
export class AddMenuComponent implements OnInit {
  
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  selected : any;
  @ViewChild('chipList',{static:false}) chipList;
  @ViewChild('resetMenuForm',{static:false}) myNgForm;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  menuForm: FormGroup;
  subjectArray: Subject[] = [];
  SectioinArray: any = ['A', 'B', 'C', 'D', 'E'];

  imageError: string;
    isImageSaved: boolean;
    cardImageBase64: string;

  ngOnInit() {
    
    this.submitBookForm();
    
  }

  constructor(
    public fb: FormBuilder,
    private router: Router,
    private ngZone: NgZone,
    private menuApi: ApiService
  ) { }

  /* Reactive book form */
  submitBookForm() {
    this.menuForm = this.fb.group({
      menu_name: ['', [Validators.required]],
      menu_price: ['', [Validators.required]],
      menuHeader: ['', [Validators.required]],
      subjects: [this.subjectArray],
      dob: ['', [Validators.required]],
      foodType: ['veg'],
      menu_desc:[''],
      menu_serves:['', [Validators.required]],
      file: ['', [Validators.required]],
    fileSource: ['', [Validators.required]]
    })
  }

  /* Add dynamic languages */
  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;
    // Add language
    if ((value || '').trim() && this.subjectArray.length < 5) {
      this.subjectArray.push({ name: value.trim() })
    }
    // Reset the input value
    if (input) {
      input.value = '';
    }
  }

  /* Remove dynamic languages */
  remove(subject: Subject): void {
    const index = this.subjectArray.indexOf(subject);
    if (index >= 0) {
      this.subjectArray.splice(index, 1);
    }
  }  

  /* Date */
  formatDate(e) {
    var convertDate = new Date(e.target.value).toISOString().substring(0, 10);
    this.menuForm.get('dob').setValue(convertDate, {
      onlyself: true
    })
  }  

  /* Get errors */
  public handleError = (controlName: string, errorName: string) => {
    return this.menuForm.controls[controlName].hasError(errorName);
  }  

  /* Submit book */
  submitMenuForm() {
    if (this.menuForm.valid) {
      this.menuApi.AddMenu(this.menuForm.value).subscribe(res => {
        this.ngZone.run(() => this.router.navigateByUrl('/menus-list'))
      });
    }
  }


  // image

  fileChangeEvent(fileInput: any) {
    this.imageError = null;
    if (fileInput.target.files && fileInput.target.files[0]) {
        // Size Filter Bytes
        const max_size = 20971520;
        const allowed_types = ['image/png', 'image/jpeg'];
        const max_height = 15200;
        const max_width = 25600;

        if (fileInput.target.files[0].size > max_size) {
            this.imageError =
                'Maximum size allowed is ' + max_size / 1000 + 'Mb';

            return false;
        }

        // if (!_.includes(allowed_types, fileInput.target.files[0].type)) {
        //     this.imageError = 'Only Images are allowed ( JPG | PNG )';
        //     return false;
        // }
        const reader = new FileReader();
        reader.onload = (e: any) => {
            const image = new Image();
            image.src = e.target.result;
            image.onload = rs => {
                const img_height = rs.currentTarget['height'];
                const img_width = rs.currentTarget['width'];

                console.log(img_height, img_width);


                if (img_height > max_height && img_width > max_width) {
                    this.imageError =
                        'Maximum dimentions allowed ' +
                        max_height +
                        '*' +
                        max_width +
                        'px';
                    return false;
                } else {
                    const imgBase64Path = e.target.result;
                    console.log("imgBase64Path"+imgBase64Path)
                    this.cardImageBase64 = imgBase64Path;
                    this.isImageSaved = true;
                    // this.previewImagePath = imgBase64Path;
                }
            };
        };

        reader.readAsDataURL(fileInput.target.files[0]);
        console.log("reader"+reader)
    }
}

removeImage() {
    this.cardImageBase64 = null;
    this.isImageSaved = false;
}


  // image

}